body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #ececec
}

#main {
  max-width: 900px;
  margin: auto;
}

#login {
  background: #ccc;
  border: 1px solid #474747;
  margin: 10px 0;
  padding: 10px;
}

.column {
  width: 48%;
  float: left;
  padding-right: 2%;
}

.column:last-of-type {
  padding: 0 0 0 2%;
}

li a span {
  font-size: 80%;
}

li a {
  text-decoration: none;
  padding: 10px 2px;
  color: #000;
}

li a: hover {
  text-decoration: underline;
}

.card {
  background: #fff;
  border: 1px solid #000;
  display: inline-block;
  margin: 10px;
  width: 112px;
  height: 175px;
  text-align: center;
  border-radius: 5px;
}

.card div:nth-of-type(1) {
  font-size: 80px;
  line-height: 165px;
}

.card div:nth-of-type(2) {
  position: relative;
  bottom: 40px;
}

.Coins {color: gold; }
.Cups {color: red; }
.Swords {color: blue; }
.Clubs {color: green; }

#hand .card:hover {
  cursor: pointer;
  transform: rotate(10deg);
}

#table, #hand {
  min-height: 250px;
}
